.sun-search-input {
  flex: 1;
  border: none;
  background: transparent;

  &::placeholder {
    font-size: 12px;
    font-weight: 400;
    color: rgba(255, 255, 255, 0.3);
  }

  &-wrap {
    height: 29px;
    background: #444454;
    border-radius: 15px;
    padding: 6px 5px 6px 15px;
    display: flex;
    align-items: center;
  }

  &-prefix {
    margin-right: 5px;
  }

  &-suffix {
    transform: translateX(5px);
  }

  &-btn {
    height: 29px;
    line-height: 29px;
    padding: 0 14px;
    background: #5a14e1;
    border-radius: 15px;
    text-align: center;
    cursor: pointer;
    color: #fff;
    font-size: 12px;
    font-weight: 600;
  }
}
