@import './var.scss';

.tr-tooltip {
  .ant-tooltip-inner {
    width: fit-content;
    padding: 0;
    border-radius: 20px;
    overflow: hidden;
    .tr-content li {
      width: 450px;
    }
  }
}

.trade-router-modal.ant-modal {
  .ant-modal-close {
    display: none;
  }
  .ant-modal-content {
    background: #4d4366;
    padding: 0;
    border-radius: 20px;
    overflow: hidden;
  }

  .ant-modal-title {
    text-align: left !important;
    font-size: 15px !important;
  }
  .ant-modal-close {
    display: none;
  }
}

.trade-router {
  font-size: 14px;
  color: #fff;
  line-height: 20px;
  cursor: pointer;
  .renderRoutrOuter {
    display: flex;
    .renderRoutrOuter-r {
      flex: 1;
    }
  }
  .trade-router-txt {
    margin-right: 10px;
    font-size: 14px;
    font-family: PingFangSC-Regular, PingFang SC;
    font-weight: 400;
    color: #ffffff;
    opacity: 0.5;
    white-space: nowrap;
  }
  .tr-arrow {
    display: inline-block;
    width: 8px;
    height: 8px;
    background: url(../images/homeTokens/ico_router_path.png) 0 0 no-repeat;
    background-size: 100% 100%;
    margin: 0 8px;
  }

  img {
    position: relative;
    width: 15px;
    margin-right: 6px;
    vertical-align: middle;
    top: -1px;
    @include mobile-only {
      width: 16px;
    }
  }
}

.tr-content {
  color: #ffffff;
  .tr-content-title {
    font-size: 20px;
    font-weight: 600;
    color: #2c3039;
  }
  .token {
    margin-right: 4px;
  }

  > p {
    display: flex;
    align-items: center;
    font-family: Avenir Next;
    font-size: 14px;
    line-height: 19px;
    color: #fff;
    margin-bottom: 16px;

    > img {
      width: 16px;
      height: 16px;
      margin-right: 4px;
    }
  }

  > div {
    min-width: 160px;
  }

  ul {
    margin: 0;
    padding: 0;
  }

  li {
    list-style: none;
    padding: 16px 16px 0 16px;
    color: #ffffff;
    position: relative;
    background: #403858;
    .tr-arrow {
      display: block;
      width: 8px;
      height: 8px;
      background: url(../images/homeTokens/arrow_default.png) 0 0 no-repeat;
      background-size: 100% 100%;
    }
    &.active {
      background: #4d4366;
      &::after {
        content: '';
        border: none;
        background: url(../images/homeTokens/router-path-checked.png) no-repeat;
        background-size: 100% 100%;
      }
    }
    &::after {
      content: '';
      position: absolute;
      top: 16px;
      right: 16px;
      width: 16px;
      height: 16px;
      border-radius: 50%;
      border: solid 1px #fff;
    }
    &:hover {
      background: #4d4366;
    }
    &:last-child {
      .tr-content-li-down {
        border-bottom: none;
      }
    }

    .tr-content-li-top {
      display: flex;
      padding-right: 22px;
      margin-bottom: 10px;
      > span {
        font-size: 15px;
        font-weight: 400;
        opacity: 0.5;
      }
      .tr-content-li-title {
        font-size: 15px;
        font-weight: 600;
        margin-right: 10px;
        opacity: 1;
        white-space: nowrap;
      }
      .tr-content-li-top-r {
        flex: 1;
        width: 0;
        color: rgba(255, 255, 255, 0.7);
        word-break: break-all;
      }
    }

    .tr-content-li-down {
      display: flex;
      flex-flow: wrap;
      font-size: 15px;
      font-weight: 400;
      border-bottom: 1px solid rgba(255, 255, 255, 0.1);
      padding-bottom: 6px;

      .tr-content-li-pair {
        display: flex;
        align-items: center;
        margin-right: 8px;
        margin-bottom: 10px;
        .tr-content-li-pair-op {
          display: flex;
          align-items: center;
          font-size: 14px;
          font-family: PingFangSC-Regular, PingFang SC;
          font-weight: 400;
          color: #ffffff;
        }
        img {
          width: 18px;
          height: 18px;
          margin-right: 4px;
        }
        &:last-child {
          margin-right: 0;

          .tr-arrow {
            display: none;
          }
        }
        .path-pool {
          align-items: center;
        }
        .tr-pool {
          margin: 0 8px;
          background: rgba(255, 255, 255, 0.1);
          color: rgba(255, 255, 255, 0.5);
          padding: 0 4px;
          border-radius: 2px;
          font-size: 11px;
        }
      }
    }
  }
}
