@import '../../../assets/css/theme';

.setting_popover.ant-popover {
  width: 272px;
  border-radius: 10px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  .ant-popover-inner {
    background: $color-info-bg;
  }
  .ant-popover-arrow {
    opacity: 0;
  }
}
.new_setting_icon {
  width: 28px;
  height: 28px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  color: #fff;
  border-radius: 50%;
  text-align: center;
  display: inline-block;
  line-height: 28px;
  cursor: pointer;
  &:hover {
    border: 1px solid $blue;
    background-color: $blue;
  }
}
