$primary-btn-color: #5A14E1;
$primary-btn-hover-color: #5a14e180;

.sun-button-primary {
  background: $primary-btn-color;
  border-color: $primary-btn-color;
  color: #fff;
  border-radius: 4px;
  height: 29px;
  width: 60px;
  line-height: 29px;
  text-align: center;
  cursor: pointer;
  &:hover {
    background: $primary-btn-hover-color;
    border-color: $primary-btn-hover-color;
    color: rgba(255, 255, 255, .8);
  }
  &:disabled, &.sun-button-weak {
    background: rgba(255, 255, 255, .3);
    border-color: rgba(255, 255, 255, .3);
    color: rgba(255, 255, 255, .5);
    cursor: no-drop;

    &:hover {
      background: rgba(255, 255, 255, .3);
      border-color: rgba(255, 255, 255, .3);
      color: rgba(255, 255, 255, .5);
    }
  }
}