.debug-box {
  z-index: 999;
  position: absolute;
  left: 0;
  bottom: -20px;
  font-size: 12px;
  color: rgb(251, 191, 191);

  .main-box {
    background: #fff;
    margin-bottom: 10px;
    padding: 5px;
    width: 350px;
    display: none;
    text-align: left;
    border-radius: 5px;
    transition: width 0.3s;
    ul li {
      display: flex;
      align-items: flex-start;
      margin-bottom: 10px;
      &:last-child {
        margin-bottom: 0;
      }
      .label {
        margin-right: 4px;
        padding: 0 3px;
        background: #666;
        font-weight: 600;
        border-radius: 3px;
        color: #fff;
      }
      .from {
        background-color: #4504fe;
      }
      .to {
        background-color: #ff9141;
      }
      .desc {
        flex: 1;
        color: #666;
      }
    }
  }
  .open {
    display: block;
  }

  .copy {
    margin-top: 10px;
  }

  .button {
    float: right;
    background: #4504fe;
    color: #fff;
    padding: 0 5px;
    cursor: pointer;
    user-select: none;
    transition: width 0.3s;
  }
}
