@import '../../../assets/css/theme';

.setting_wrap {
  display: flex;
  align-items: center;
  margin-right: 10px;
}

.setting_label {
  font-size: 16px;
  font-family: PingFangSC-Semibold, PingFang SC;
  font-weight: 600;
  color: $white;
}

.setting_btns {
  display: flex;
  margin: 12px 0 12px 0;
  .setting_btn_wrap {
    border: 1px solid rgba($white, 0.3);
    border-radius: 2px;
    height: 32px;
  }
  button {
    background: transparent;
    font-size: 14px;
    font-family: PingFangSC-Regular, PingFang SC;
    font-weight: 400;
    color: $white;
    border: none;
    cursor: pointer;
    height: 30px;
    line-height: 14px;
    &:nth-child(3) {
      border-radius: 0px 2px 2px 0px;
    }
  }
  .setting_input {
    margin-left: 20px;
  }
  .active {
    background: $purple;
    border: none;
  }
}

.setting_input.module_important {
  background-color: rgba(255, 255, 255, 0.1);
  color: $white;
  width: 70px;
  border-radius: 2px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  padding: 3px 11px;
  height: 30px;
  &:focus,
  &:hover {
    border: 1px solid #5915e1;
    box-shadow: none;
  }
  input {
    background: transparent;
    padding: 5px 10px;
    font-size: 14px;
    font-family: PingFangSC-Regular, PingFang SC;
    font-weight: 400;
    color: $white;
    opacity: 0.7;
  }
}

.info_text {
  color: $orange;
  margin-bottom: 16px;
}

.setting_minutes {
  font-size: 14px;
  font-family: PingFangSC-Regular, PingFang SC;
  font-weight: 400;
  color: $white;
  margin-left: 10px;
}
